function TradeSummary({ trade, allowedSlippage }: { trade: Trade; allowedSlippage: number }) {
  const { chainId } = useWeb3React()
  const theme = useContext(ThemeContext)
  const priceImpact  = trade?.priceImpact
  const isExactIn = trade.tradeType === TradeType.EXACT_INPUT
  const slippageAdjustedAmounts = computeSlippageAdjustedAmounts(trade, allowedSlippage)

  return (
    <>
      <AutoColumn style={{ padding: '0 20px' }}>
        <RowBetween>
          <RowFixed lineHeight={'24px'} >
            <TYPE.Black fontSize={14} fontWeight={400} color={theme.text2}>
              {isExactIn ? 'Minimum received' : 'Maximum sold'}
              <QuestionHelper bkgOff={true} small={'s'} info={<>Your transaction will revert if there is a large, unfavorable price movement before it is confirmed.</>} />
            </TYPE.Black>
          </RowFixed>
          <RowFixed>
            <TYPE.Black color={theme.text1} fontSize={14}>
              {isExactIn
                ? `${slippageAdjustedAmounts[Field.OUTPUT]?.toSignificant(4)} ${trade.outputAmount.currency.getSymbol(chainId)}` ??
                  '-'
                : `${slippageAdjustedAmounts[Field.INPUT]?.toSignificant(4)} ${trade.inputAmount.currency.getSymbol(chainId)}` ??
                  '-'}
            </TYPE.Black>
          </RowFixed>
        </RowBetween>
        <RowBetween>
          <RowFixed lineHeight={'24px'} >
            <TYPE.Black fontSize={14} fontWeight={400} color={theme.text2}>
              Price Impact
              <QuestionHelper bkgOff={true} small={'s'} info={<>The difference between the market price and estimated price due to trade size.</>} />
            </TYPE.Black>
          </RowFixed>
          <FormattedPriceImpact priceImpact={priceImpact} />
        </RowBetween>
        <RowBetween>
          <RowFixed lineHeight={'24px'} >
            <TYPE.Black fontSize={14} fontWeight={400} color={theme.text2}>
              Liquidity Provider Fee
              <QuestionHelper bkgOff={true} small={'s'} info={<>FeSwap do NOT charge liquidity provider fee to exchange users, which could be 0.25-0.3% in 
                                  other DEXs. Instead we automatically capture profits for our liquidity providers 
                                  from the crypto intrinsic variability.</>} />
            </TYPE.Black>
          </RowFixed>
          <TYPE.Black fontSize={14} color={theme.primary1}>
            FREE
          </TYPE.Black>
        </RowBetween>
      </AutoColumn>
    </>
  )
}

export interface AdvancedSwapDetailsProps {
  trade?: Trade
}

export function AdvancedSwapDetails({ trade }: AdvancedSwapDetailsProps) {
  const theme = useContext(ThemeContext)

  const [allowedSlippage] = useUserSlippageTolerance()

  const showRoute = Boolean(trade && trade.route.path.length > 2)

  return (
    <AutoColumn>
      {trade && (
        <>
          <TradeSummary trade={trade} allowedSlippage={allowedSlippage} />
          {showRoute && (
              <AutoColumn style={{ padding: '0 20px' }}>
                <RowFixed lineHeight={'24px'}>
                  <TYPE.Black fontSize={14} fontWeight={400} color={theme.text2}>
                    Route
                    <QuestionHelper bkgOff={true} small={'s'} info={<>Routing through these tokens resulted in the best price for your trade.</>} />
                  </TYPE.Black>
                </RowFixed>
                <SwapRoute trade={trade} />
              </AutoColumn>
          )}
        </>
      )}
    </AutoColumn>
  )
}

