export function useRECIssuerAddress(): string {
  const [simulationMode] = useSimulationMode()
  const { chainId } = useWeb3React()

  return useMemo(() => {
    return chainId === ChainId.MATIC_TESTNET
      ? simulationMode
        ? defaultIssuerAddress
        : defaultIssuerAddressTest
      : simulationMode
      ? defaultIssuerAddressMatic
      : defaultIssuerAddressMatic // defaultIssuerAddressMaticRelease
  }, [simulationMode, chainId])
}

